<template>
  <div class="bottom-fix">
    <div class="container">
      <button class="edit-button" type="button" @click="editList(true)">
        Edit grocery list
      </button>
      <button
        type="button"
        class="btn btn_lg btn_max"
        @click="sendData"
        :disabled="sending"
        :style="{
          backgroundColor: this.getThemeColor,
        }"
      >
        {{ sending ? 'Sending...' : 'Update list' }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import firebase from 'firebase/app'

export default {
  name: 'BottomPanel',

  data() {
    return {
      sending: false,
    }
  },

  computed: {
    ...mapGetters(['getProdList', 'getThemeColor']),
  },

  methods: {
    ...mapActions(['addNotificationTiming']),
    ...mapMutations(['editList', 'clearSearchText']),

    async sendData() {
      this.sending = true

      try {
        await firebase
          .database()
          .ref('list')
          .remove()

        await firebase
          .database()
          .ref('list')
          .push(this.getProdList)

        this.editList(false)
        this.clearSearchText()

        this.addNotificationTiming({
          status: 'sent',
          message: 'Data saved successfully',
        })
      } catch {
        this.addNotificationTiming({
          status: 'error',
          message: 'Error',
        })
      }

      this.sending = false
    },
  },
}
</script>
